import Layer from "../../common/cmpt/base/Layer";
import ResSprite from "../../common/cmpt/ui/res/ResSprite";
import { DirUrl, ResUrl } from "../../common/const/Url";
import GlobalInfo from "../../common/runtime/GlobalInfo";
import UserInfo, { UserInfoStorge } from "../../common/runtime/UserInfo";
import { eventsOnLoad } from "../../common/util/Events";
import Res from "../../common/util/Res";
import Tool from "../../common/util/Tool";
import DlgAnimValue from "../dialog/DlgAnimValue";
import DlgAudio from "../dialog/DlgAudio";
import DlgCircleList from "../dialog/DlgCircleList";
import DlgEvents from "../dialog/DlgEvents";
import DlgI18N from "../dialog/DlgI18N";
import DlgLayer from "../dialog/DlgLayer";
import DlgLoopList from "../dialog/DlgLoopList";
import DlgMultiTexture from "../dialog/DlgMultiTexture";
import DlgShader from "../dialog/DlgShader";
import DlgShake from "../dialog/DlgShake";
import DlgTimer from "../dialog/DlgTimer";
import DlgVirtualList from "../dialog/DlgVirtualList";
import DlgVirtualList2 from "../dialog/DlgVirtualList2";

const { ccclass, property } = cc._decorator;

@ccclass
@eventsOnLoad()
export default class Game extends cc.Component {

    @property(cc.ProgressBar) playerBloodBar: cc.ProgressBar = null
    @property(cc.ProgressBar) enemyBloodBar: cc.ProgressBar = null
    @property(cc.Node) playerNode: cc.Node = null
    @property(cc.Node) enemyNode: cc.Node = null
    @property(cc.Node) enemyHeadNode: cc.Node = null
    @property(cc.Node) gameoverPage: cc.Node = null
    private playerLife: number = null
    private maxPlayerLife: number = null
    private enemyLife: number = null
    private maxEnemyLife: number = null
    private playerAtk: number = null
    private enemyAtk: number = null

    private moveTime: number = null
    private playerPos: cc.Vec3 = null
    private enemyPos: cc.Vec3 = null

    async start() {
        await this.init()
        await Tool.waitCmpt(this, 1)
        this.playerAtkEvent()
    }

    protected async init() {
        let levelSelect = GlobalInfo.LEVEL_SELECT;
        const resSpriteEnemy = this.enemyNode.getComponent(ResSprite) || this.enemyNode.addComponent(ResSprite);
        const urlEnemy = DirUrl.TEXTURE + `game/enemy/${levelSelect}`;

        const resSpriteEnemyHead = this.enemyHeadNode.getComponent(ResSprite) || this.enemyHeadNode.addComponent(ResSprite);
        const urlEnemyHead = DirUrl.TEXTURE + `game/enemy/head/${levelSelect}`;

        await resSpriteEnemy.setSpriteFrame(urlEnemy);
        await resSpriteEnemyHead.setSpriteFrame(urlEnemyHead);

        this.maxEnemyLife = this.enemyLife = levelSelect * 1000 + 1000;
        this.maxPlayerLife = this.playerLife = UserInfo.instance.roleConfig[0];

        this.enemyAtk = levelSelect * 100 + 100;
        this.playerAtk = UserInfo.instance.roleConfig[1];

        this.moveTime = 0.5
        this.playerPos = this.playerNode.position;
        this.enemyPos = this.enemyNode.position;
        this.renewBlood();
    }

    renewBlood() {
        this.playerBloodBar.progress = this.playerLife / this.maxPlayerLife
        this.enemyBloodBar.progress = this.enemyLife / this.maxEnemyLife
    }

    playerAtkEvent() { //玩家攻击 倍数
        cc.tween(this.playerNode)
            .to(this.moveTime, { position: cc.v3(this.enemyPos.x - 300, this.enemyPos.y) })
            .call(() => {
                let value = this.playerAtk
                this.enemyHurtEvent(value)
            })
            .to(this.moveTime, { position: this.playerPos })
            .start()
    }

    enemyHurtEvent(value: number) {
        this.enemyLife -= value
        this.renewBlood()
        if (this.enemyLife <= 0) {
            this.victory()
            return
        }
        cc.tween(this.enemyNode)
            .to(0.1, { position: cc.v3(this.enemyPos.x + 30, this.enemyPos.y), color: cc.color(255, 0, 0) })
            .to(0.1, { position: this.enemyPos, color: cc.color(255, 255, 255) })
            .delay(1)
            .call(() => {
                this.enemyAtkEvent()
            })
            .start()
    }

    enemyAtkEvent() {
        cc.tween(this.enemyNode)
            .to(this.moveTime, { position: cc.v3(this.playerPos.x + 300, this.playerPos.y) })
            .call(() => {
                let value = this.enemyAtk
                this.playerHurtEvent(value)
            })
            .to(this.moveTime, { position: this.enemyPos })
            .start()
    }

    playerHurtEvent(value: number) {
        this.playerLife -= value
        this.renewBlood()
        if (this.playerLife <= 0) {
            this.fail()
            return
        }
        cc.tween(this.playerNode)
            .to(0.1, { position: cc.v3(this.playerPos.x - 30, this.playerPos.y), color: cc.color(255, 0, 0) })
            .to(0.1, { position: this.playerPos, color: cc.color(255, 255, 255) })
            .delay(1)
            .call(() => {
                this.playerAtkEvent()
            })
            .start()
    }


    victory() {
        this.gameoverPage.active = true
        this.gameoverPage.getChildByName('win').active = true
        UserInfo.instance.coin += 500

        if (GlobalInfo.LEVEL_SELECT === UserInfo.instance.levelProgress) {
            UserInfo.instance.levelProgress += 1
        }
        UserInfoStorge.setUserInfo()
    }

    fail() {
        this.gameoverPage.active = true
        this.gameoverPage.getChildByName('fail').active = true

        UserInfo.instance.coin += 200
        UserInfoStorge.setUserInfo()
    }

    private onClickHome() {
        Layer.inst.enterMain(ResUrl.PREFAB.HOME);
    }

    private onClickTimer() {
        Layer.inst.openUniDialogAsync(DlgTimer.pUrl);
    }

    private onClickLayer() {
        Layer.inst.openUniDialogAsync(DlgLayer.pUrl);
    }

    private onClickShader() {
        Layer.inst.openUniDialogAsync(DlgShader.pUrl);
    }

    private onClickAnimValue() {
        Layer.inst.openUniDialogAsync(DlgAnimValue.pUrl);
    }

    private onClickButton() {
        Layer.inst.openUniDialogAsync(DirUrl.PREFAB_DIALOG + "DlgButton");
    }

    private onClickShake() {
        Layer.inst.openUniDialogAsync(DlgShake.pUrl);
    }

    private onClickVirtualList() {
        Layer.inst.openUniDialogAsync(DlgVirtualList.pUrl);
    }

    private onClickVirtualList2() {
        Layer.inst.openUniDialogAsync(DlgVirtualList2.pUrl);
    }

    private onClickLoopList() {
        Layer.inst.openUniDialogAsync(DlgLoopList.pUrl);
    }

    private onClickCircleList() {
        Layer.inst.openUniDialogAsync(DlgCircleList.pUrl);
    }

    private async onClickAudio() {
        Layer.inst.openUniDialogAsync(DlgAudio.pUrl);
        Layer.inst.showLoading();
        await Res.loadDir(DirUrl.AUDIO, cc.AudioClip);
        Layer.inst.hideLoading();
    }

    private onClickI18N() {
        Layer.inst.openUniDialogAsync(DlgI18N.pUrl);
    }

    private onClickEvent() {
        Layer.inst.openUniDialogAsync(DlgEvents.pUrl);
    }

    private onClickMultiTexture() {
        Layer.inst.openUniDialogAsync(DlgMultiTexture.pUrl);
    }
}
